<template>
  <div>
    
    <div class="container">
      <div 
        class="banner-wrap"
        @mousemove="handleMouseMove"
        @mouseenter="handleMouseEnter"
        @mouseleave="handleMouseLeave"
        ref="bannerWrap"
      >
        <div class="banner" :style="bannerStyle">
          <div class="banner-bg" :style="bannerBgStyle"></div>
          <div class="banner-info">
            <h1 class="h1 montserrat-bold text-white">–ù–∞–π–¥—ë–º —á—Ç–æ –Ω—É–∂–Ω–æ</h1>
            <p class="p montserrat-regular text-gray">–û—Ç–≤–µ—Ç—å—Ç–µ –Ω–∞ –ø–∞—Ä—É –≤–æ–ø—Ä–æ—Å–æ–≤ ‚Äî –∞–ª–≥–æ—Ä–∏—Ç–º –ø–æ–¥–±–µ—Ä–µ—Ç –Ω–∞–±–æ—Ä –ø–æ–¥ —Ö–∞—Ä–∞–∫—Ç–µ—Ä</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Banner',
  data() {
    return {
      width: 0,
      height: 0,
      mouseX: 0,
      mouseY: 0,
      mouseLeaveDelay: null,
      imageUrl: require('@/assets/img/banner.png')
    }
  },
  mounted() {
    this.width = this.$refs.bannerWrap.offsetWidth;
    this.height = this.$refs.bannerWrap.offsetHeight;
  },
  computed: {
    mousePX() {
      return this.mouseX / this.width;
    },
    mousePY() {
      return this.mouseY / this.height;
    },
    bannerStyle() {
      const rX = this.mousePX * 30;
      const rY = this.mousePY * -30;
      return {
        transform: `rotateY(${rX}deg) rotateX(${rY}deg)`
      };
    },
    bannerBgStyle() {
      const tX = this.mousePX * -40;
      const tY = this.mousePY * -40;
      return {
        transform: `translateX(${tX}px) translateY(${tY}px)`,
        backgroundImage: `url(${this.imageUrl})`
      };
    }
  },
  methods: {
    handleMouseMove(e) {
      this.mouseX = e.pageX - this.$refs.bannerWrap.offsetLeft - this.width / 2;
      this.mouseY = e.pageY - this.$refs.bannerWrap.offsetTop - this.height / 2;
    },
    handleMouseEnter() {
      clearTimeout(this.mouseLeaveDelay);
    },
    handleMouseLeave() {
      this.mouseLeaveDelay = setTimeout(() => {
        this.mouseX = 0;
        this.mouseY = 0;
      }, 1000);
    }
  }
}
</script>

<style scoped>
body {
  -webkit-font-smoothing: antialiased;
}




.banner-wrap {
  transform: perspective(800px);
  transform-style: preserve-3d;
  cursor: pointer;
}

.banner-wrap:hover .banner-info {
  transform: translateY(0);
}

.banner-wrap:hover .banner-info p {
  opacity: 1;
}

.banner-wrap:hover .banner-info,
.banner-wrap:hover .banner-info p {
  transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}

.banner-wrap:hover .banner-info:after {
  transition: 5s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 1;
  transform: translateY(0);
}

.banner-wrap:hover .banner-bg {
  transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1), opacity 5s cubic-bezier(0.23, 1, 0.32, 1);
  opacity: 0.8;
}

.banner-wrap:hover .banner {
  transition: 0.6s cubic-bezier(0.23, 1, 0.32, 1), box-shadow 2s cubic-bezier(0.23, 1, 0.32, 1);
  box-shadow: rgba(255, 255, 255, 0.2) 0 0 40px 5px, 
              white 0 0 0 1px, 
              rgba(0, 0, 0, 0.66) 0 30px 60px 0, 
              inset #333 0 0 0 5px, 
              inset white 0 0 0 6px;
}

.banner {
  position: relative;
  flex: 0 0;
  width: 100%;
  height: 320px;
  background-color: #333;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.66) 0 30px 60px 0, 
              inset #333 0 0 0 5px, 
              inset rgba(255, 255, 255, 0.5) 0 0 0 6px;
  transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.banner-bg {
  /* opacity: 0.5; */
  position: absolute;
  top: -20px;
  left: -20px;
  width: 105%;
  height: 110%;
  padding: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: 1s cubic-bezier(0.445, 0.05, 0.55, 0.95), 
              opacity 5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  pointer-events: none;
}

.banner-info {
  padding: 20px;
  position: absolute;
  bottom: 0;
  color: #fff;
  transform: translateY(40%);
  transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.banner-info p {
  opacity: 0;
  text-shadow: black 0 2px 3px;
  transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.banner-info * {
  position: relative;
  z-index: 1;
}

.banner-info:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.6) 100%);
  background-blend-mode: overlay;
  opacity: 0;
  transform: translateY(100%);
  transition: 5s 1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

</style>