    <div class="filters-section">
      <div class="search-bar">
        <input 
          v-model="searchQuery"
          type="text"
          placeholder="–ü–æ–∏—Å–∫ –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é..."
          class="search-input montserrat-regular"
        />
        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="#888" stroke-width="2">
          <circle cx="11" cy="11" r="8"/>
          <path d="m21 21-4.35-4.35"/>
        </svg>
      </div>

      <button 
        class="filters-toggle montserrat-medium"
        @click="showFilters = !showFilters"
      >
        <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
          <line x1="4" y1="21" x2="4" y2="14"/>
          <line x1="4" y1="10" x2="4" y2="3"/>
          <line x1="12" y1="21" x2="12" y2="12"/>
          <line x1="12" y1="8" x2="12" y2="3"/>
          <line x1="20" y1="21" x2="20" y2="16"/>
          <line x1="20" y1="12" x2="20" y2="3"/>
          <line x1="1" y1="14" x2="7" y2="14"/>
          <line x1="9" y1="8" x2="15" y2="8"/>
          <line x1="17" y1="16" x2="23" y2="16"/>
        </svg>
        –§–∏–ª—å—Ç—Ä—ã
        <span v-if="activeFiltersCount > 0" class="filter-badge">{{ activeFiltersCount }}</span>
      </button>
    </div>

    <!-- –ü–∞–Ω–µ–ª—å —Ñ–∏–ª—å—Ç—Ä–æ–≤ -->
    <div v-if="showFilters" class="filters-panel">
      <!-- –¶–µ–Ω–∞ -->
      <div class="filter-group">
        <h3 class="p montserrat-semibold white-text">–¶–µ–Ω–∞</h3>
        <div class="price-inputs">
          <input 
            v-model.number="priceRange.min"
            type="number"
            placeholder="–û—Ç"
            class="price-input montserrat-regular"
          />
          <span class="gray-text">‚Äî</span>
          <input 
            v-model.number="priceRange.max"
            type="number"
            placeholder="–î–æ"
            class="price-input montserrat-regular"
          />
        </div>
      </div>

      <!-- –¢–µ–≥–∏ -->
      <div class="filter-group">
        <h3 class="p montserrat-semibold white-text">–ö–∞—Ç–µ–≥–æ—Ä–∏–∏</h3>
        <div class="tags-list">
          <label 
            v-for="tag in availableTags" 
            :key="tag"
            class="tag-checkbox montserrat-regular"
          >
            <input 
              type="checkbox" 
              :value="tag"
              v-model="selectedTags"
            />
            <span>{{ tag }}</span>
          </label>
        </div>
      </div>

      <!-- –°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ -->
      <div class="filter-group">
        <h3 class="p montserrat-semibold white-text">–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞</h3>
        <select v-model="sortBy" class="sort-select montserrat-regular">
          <option value="default">–ü–æ —É–º–æ–ª—á–∞–Ω–∏—é</option>
          <option value="price-asc">–¶–µ–Ω–∞: –ø–æ –≤–æ–∑—Ä–∞—Å—Ç–∞–Ω–∏—é</option>
          <option value="price-desc">–¶–µ–Ω–∞: –ø–æ —É–±—ã–≤–∞–Ω–∏—é</option>
        </select>
      </div>

      <!-- –ö–Ω–æ–ø–∫–∞ —Å–±—Ä–æ—Å–∞ -->
      <button 
        v-if="activeFiltersCount > 0"
        @click="resetFilters"
        class="reset-btn montserrat-medium"
      >
        –°–±—Ä–æ—Å–∏—Ç—å —Ñ–∏–ª—å—Ç—Ä—ã
      </button>
    </div>

    <!-- –†–µ–∑—É–ª—å—Ç–∞—Ç—ã -->
    <div class="results-info montserrat-regular gray-text">
      –ù–∞–π–¥–µ–Ω–æ –±–æ–∫—Å–æ–≤: {{ filteredBoxes.length }}
    </div>

    <!-- –°–µ—Ç–∫–∞ –∫–∞—Ä—Ç–æ—á–µ–∫ -->
    <div class="cards-wrapper">
      <Card 
        v-for="box in displayedBoxes" 
        :key="box.id"
        :box="box"
      />
    </div>

    <!-- –ö–Ω–æ–ø–∫–∞ "–ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë" -->
    <div v-if="hasMore" class="load-more-container">
      <button 
        @click="loadMore"
        class="load-more-btn montserrat-semibold"
      >
        –ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë
      </button>
    </div>

    <!-- –ü—É—Å—Ç–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ -->
    <div v-if="filteredBoxes.length === 0" class="empty-state">
      <p class="p montserrat-regular gray-text">
        –ü–æ –≤–∞—à–µ–º—É –∑–∞–ø—Ä–æ—Å—É –Ω–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ üòî
      </p>
      <button @click="resetFilters" class="button-second montserrat-semibold">
        –°–±—Ä–æ—Å–∏—Ç—å —Ñ–∏–ª—å—Ç—Ä—ã
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue'
import Card from '@/components/Card.vue'
import { boxes, calculateBoxPrice, getShortComposition } from '@/data/boxes'

// –°–æ—Å—Ç–æ—è–Ω–∏–µ
const searchQuery = ref('')
const showFilters = ref(false)
const priceRange = ref({ min: null, max: null })
const selectedTags = ref([])
const sortBy = ref('default')
const itemsPerPage = 16
const currentPage = ref(1)

// –í—Å–µ –¥–æ—Å—Ç—É–ø–Ω—ã–µ —Ç–µ–≥–∏
const availableTags = computed(() => {
  const tags = new Set()
  boxes.forEach(box => {
    box.tags.forEach(tag => tags.add(tag))
  })
  return Array.from(tags).sort()
})

// –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –∞–∫—Ç–∏–≤–Ω—ã—Ö —Ñ–∏–ª—å—Ç—Ä–æ–≤
const activeFiltersCount = computed(() => {
  let count = 0
  if (searchQuery.value) count++
  if (priceRange.value.min || priceRange.value.max) count++
  if (selectedTags.value.length > 0) count++
  if (sortBy.value !== 'default') count++
  return count
})

// –§–∏–ª—å—Ç—Ä–∞—Ü–∏—è –∏ —Å–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞
const filteredBoxes = computed(() => {
  let result = [...boxes]

  // –ü–æ–∏—Å–∫ –ø–æ –Ω–∞–∑–≤–∞–Ω–∏—é
  if (searchQuery.value) {
    const query = searchQuery.value.toLowerCase()
    result = result.filter(box => 
      box.name.toLowerCase().includes(query) ||
      box.tagline.toLowerCase().includes(query)
    )
  }

  // –§–∏–ª—å—Ç—Ä –ø–æ —Ü–µ–Ω–µ
  if (priceRange.value.min || priceRange.value.max) {
    result = result.filter(box => {
      const price = calculateBoxPrice(box.id)
      const minOk = !priceRange.value.min || price >= priceRange.value.min
      const maxOk = !priceRange.value.max || price <= priceRange.value.max
      return minOk && maxOk
    })
  }

  // –§–∏–ª—å—Ç—Ä –ø–æ —Ç–µ–≥–∞–º (–ª—é–±–æ–π –∏–∑ –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö)
  if (selectedTags.value.length > 0) {
    result = result.filter(box =>
      box.tags.some(tag => selectedTags.value.includes(tag))
    )
  }

  // –°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞
  if (sortBy.value === 'price-asc') {
    result.sort((a, b) => calculateBoxPrice(a.id) - calculateBoxPrice(b.id))
  } else if (sortBy.value === 'price-desc') {
    result.sort((a, b) => calculateBoxPrice(b.id) - calculateBoxPrice(a.id))
  }

  return result
})

// –û—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã–µ –±–æ–∫—Å—ã (—Å –ø–∞–≥–∏–Ω–∞—Ü–∏–µ–π)
const displayedBoxes = computed(() => {
  return filteredBoxes.value.slice(0, currentPage.value * itemsPerPage)
})

// –ï—Å—Ç—å –ª–∏ –µ—â—ë –±–æ–∫—Å—ã
const hasMore = computed(() => {
  return displayedBoxes.value.length < filteredBoxes.value.length
})

// –ó–∞–≥—Ä—É–∑–∏—Ç—å –µ—â—ë
function loadMore() {
  currentPage.value++
}

// –°–±—Ä–æ—Å–∏—Ç—å —Ñ–∏–ª—å—Ç—Ä—ã
function resetFilters() {
  searchQuery.value = ''
  priceRange.value = { min: null, max: null }
  selectedTags.value = []
  sortBy.value = 'default'
  currentPage.value = 1
}

// –°–±—Ä–æ—Å–∏—Ç—å –ø–∞–≥–∏–Ω–∞—Ü–∏—é –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ñ–∏–ª—å—Ç—Ä–æ–≤
import { watch } from 'vue'
watch([searchQuery, priceRange, selectedTags, sortBy], () => {
  currentPage.value = 1
}, { deep: true })
</script>

